import React, { useState } from 'react';
import { TextField, Button } from '@material-ui/core';

const VideoPresent = ({ smallScreen }) => {
    const [showInput, setShowInput] = useState(false);
    const [inputValue, setInputValue] = useState('');
    const [inputError, setInputError] = useState(false);

    const handleSubmit = (e) => {
        e.preventDefault();
        if (inputValue == 'martinamandarina') openInNewTab('https://www.youtube.com/watch?v=Dy94Ktxd7h8&feature=youtu.be');
        else setInputError(true);
    }

    function openInNewTab(url) {
        var win = window.open(url, '_blank');
        win.focus();
    }

    return (
        <div className="mt-3">
            <h2>Yuhuuuu, felicitats!! Ja casi ho tens... üòã</h2>
            {!showInput ?
                <Button
                    type="button"
                    variant="contained"
                    className={`ml-2 ${smallScreen ? 'mt-2' : ''}`}
                    onClick={() => setShowInput(true)}
                >
                    Obrir regal üéÅ
                </Button>
                : <form onSubmit={handleSubmit}>

                    <TextField
                        id="openPresent"
                        variant="outlined"
                        label="Contrasenya"
                        size="small"
                        helperText="Necessitar√†s una contrasenya... el puzzle que acabes de resoldre t'hauria d'ajudar!"
                        value={inputValue}
                        error={inputError}
                        onChange={(e) => { setInputValue(e.target.value); setInputError(false); }}
                    />
                    <Button
                        type="submit"
                        variant="contained"
                        className={`ml-2`}
                        onClick={() => setShowInput(true)}
                    >
                        Obrir üéÅ
                </Button>
                </form>
            }
        </div>
    )
}

export default VideoPresent;